body {
  background-image: url("./images/Marsview.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;

}
.App {
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
.climateData {
  height: 33.33%;
  width: 100%;
  border-top: 1px solid white;
}
.closeItem {
  width: 50px;
  height: 50px;
  font-size: 50px;
  padding: 0 10px;
  display: block;
  position: absolute;
  top:0;
  left:0;
}
.pageTitle {
  margin: 0 auto;
  text-align: center;
  color: white;
  width: 100vw;
}
.solBlock {
  background-color: rgba(14, 16, 24, 0.9);
  color: white;
  padding: 10px;
  font-size: 18px;
  width: 320px;
  border: 1px solid white;
  opacity: 0.9;
}
.solBlock:nth-child(1) {
  background-color: rgba(32, 0, 65, 0.9);
}
.solBlock:hover {
  background-color: rgba(76, 42, 9, 0.9);
}
.solContainer {
  position: absolute;
  height: 100vh;
  overflow-y: scroll;
  top: 0;
}
.solDetails {
  text-align: center;
  color: white;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(14, 16, 24, 0.9);
  width: 320px;
  position: absolute;
}
.solTitle {
  border-bottom: 1px solid white;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
@media screen and (min-width: 320px) {
  .solBlock {
    width: 100vw;
    background-color: rgba(14, 16, 24, 1);
    padding: 10px 0;
    border: 0;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    position: relative;
  }
  .solContainer {
    margin-top: 65px;
    height: calc(100vh - 70px);
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
  .solContainer::-webkit-scrollbar {
    display: none;
  }
  .solDetails {
    width: 100vw;
    height: 100vh;
    margin-top: -65px;
    padding: 0;
  }
  .pageTitle {
    background-color: rgba(14, 16, 24, 1);
    font-size: 48px;
  }
  .solBlock .solSensor p {
    padding: 0 10px;
  }
}
@media screen and (min-width: 767px) {
  .arrangeClimateData {
    display: flex;
  }
  .arrangeClimateData .climateData h2 {
    font-size: 18px;
  }
  .closeItem {
    display: none;
  }
  .solBlock {
    width: 200px;
    font-size: 18px;
    border: 0;
    border: 1px solid white;
  }
  .solBlock .solSensor p {
    padding: 0;
  }
  .solContainer {
    margin-top: 0;
    height: 100vh;
  }
  .solDetails {
    position: absolute;
    left: 60vw;
    width: 50vw;
    height: auto;
    margin-top: 0;
  }
  .solSensor {
    font-size: 14px;
    padding: 0 10px
  }
  .pageTitle {
    left: 10vw;
    position: relative;
    background-color: transparent;
    font-size: 60px;
    width: 500px;
  }
}
@media screen and (min-width: 1279px) {
  .solBlock {
    width: 320px;
  }
}
